.blockLogin {
    width: 413px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #f8fcfc;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    margin: 60px auto 0;
    padding-bottom: 50px;
}

.blockInfoLogIn {
    display: flex;
    width: 250px;
    flex-direction: column;
    margin-bottom: 30px;
    margin-right: 35px;
    font-family: dm sans, sans-serif;
    color: #6c757d;
}

.someInfo {
    align-self: flex-end;
}